article {
 margin-bottom: 50px;
 padding: 0 30px;
 width: 100%;

 &:last-child {
 margin-bottom: 0;
 }

 .title {
 color: #333333;
 font-size: 35px;
 line-height: 45px;
 font-weight: 700;
 margin-top: 24px;
 }

 .info {
 font-size: 14px;
 line-height: 30px;
 font-weight: 500;
 padding: 19px 0 10px;
 color: #8c8c8c;
 }

}
